$color-main: #fff; //+
$color-secondary: #191919;//+
$color-black: #000;//+
$color-light-black: #121212;//+
$color-dark: #272727;//+
$color-gray: #6C7176;//+
$color-light-gray: #EBEBEB;//+
$color-paragraph: #F2F2F2;//+
$color-title: #B3B3B3;//+
$color-subtitle: #787878;
$color-border:#434343; //+
$color-warning: #ffe900;
$color-danger: #E44002;//+
$color-info: #017FDD;//+
$color-success: #40D674;//+
$color-background: #363636;//+
$color-copyright: #8E41EE;//+
$color-purple: #9233F0;//+

@mixin classic-flex {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}

@mixin content-block-absolute {
    content: '';
    display: block;
    position: absolute;
}
 
@mixin center {
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%)
}

@mixin line-letter_banner {
    @include content-block-absolute;    
    background: url('../img/line-letter.png') no-repeat center / cover;
    left: -40px;
    top: -62px;
    width: 72px;
    height: 146px;
} @mixin line-letter_clients-results {
    @include content-block-absolute;
    background: url('../img/line-letter-mini.png') no-repeat center / cover;
    left: -20px;
    top: -10px;
    width: 28.5px;
    height: 59.5px;
} @mixin line-letter_what-choose {
    background: url('../img/line-letter-mini.png') no-repeat center / cover;
    @include content-block-absolute;    
    left: -20px;
    top: -14px;
    width: 28.5px;
    height: 59.5px;
} @mixin line-letter_form {
    background: url('../img/line-letter-mini.png') no-repeat center / cover;
    @include content-block-absolute;    
    left: -18px;
    top: -17px;
    width: 28.5px;
    height: 59.5px;
} @mixin line-letter_form {
    @include content-block-absolute;    
    background: url('../img/line-letter-mini.png') no-repeat center / cover;
    left: -21px;
    top: -17px;
    width: 28.5px;
    height: 59.5px;
}

@mixin addres-gps-icon {
    @include content-block-absolute;
    background: url('../img/icons/address_gps-icon.svg') no-repeat center / cover;
    left: 0;
    width: 11px;
    height: 16px;
}

@mixin mini-brief-title-icon {
    @include content-block-absolute;
    background: url('../img/icons/male-technologist.png') no-repeat center / cover;
    width: 21px;
    height: 21px;
    top: 0;
    right: 0;
}

@mixin line-under {
    @include content-block-absolute;
    width: 100%;
    height: 25px;
    bottom: 0;
    right: 0;
    transform: skewX(-20deg);
    z-index: -1;
} @mixin line-under_digital {
    @include line-under;
    background: url('../img/line-under_orange.png') no-repeat center / cover;
    width: 70%;
} @mixin line-under_ready {
    @include line-under;
    background: url('../img/line-under_purple.png') no-repeat center / cover;
}

@mixin line-over {
    @include content-block-absolute;
    width: 64px;
    height: 4px;
    background: linear-gradient(180deg, #DB00FF -62.5%, #6651E7 100%);
    border-radius: 4px;
    top: 0;
} @mixin line-over_left {
    @include line-over;
    left: 0;
} @mixin line-over_center {
    @include line-over;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

@mixin what-choose-face {
    @include content-block-absolute;
    background: url('../img/icons/sticker.png') no-repeat center / cover;
    width: 65px;
    height: 65px;
    right: 0;
    top: 0;
}

@mixin bread-circle {
    @include content-block-absolute;
    border-radius: 50%;
    background-color: $color-purple;
    right: 15px;
    width: 4px;
    height: 4px;
}

@mixin scroll-arrow {
    @include content-block-absolute;
    background: url('../img/icons/arrow.png') no-repeat center / cover;
    width: 12px;
    height: 18px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

@mixin arrows-loading {
    @include content-block-absolute;
    background: url('../img/icons/arrows-loading.png') no-repeat center / cover;
    width: 40px;
    height: 40px;
    bottom: 15%;
    left: 0;
}


@mixin triangle_navigation-link {
    @include content-block-absolute;
    position: absolute;
    right: -20px;
    top: 3px;
    padding: 3px 3px;
    border-top: 2px solid #fff;
    border-right: 2px solid #fff;
    transform: rotate(135deg);
    @media screen and (max-width: 991px) {
        top: 15px;
        right: 10px;
        padding: 4px 4px;
    }
}

@mixin card-line {
    @include content-block-absolute;
    background: url('../img/icons/card-line.png') no-repeat center / cover;
    width: 38px;
    height: 216px;
} @mixin card-line_left {
    @include card-line;
    left: -30px;
    top: 60px;
} @mixin card-line_right {
    @include card-line;
    right: -30px;
    bottom: 50px;
    transform: scale(-1, 1);
    transform: rotate(180deg);
}

@mixin header-square {
    @include content-block-absolute;
    top: -100px;
    right: -20px;
    transform: rotate(-75deg);
    border: 1px solid rgba(174, 99, 207, 0.8);
    width: 258px;
    height: 258px;
} 

@mixin link-line {
    @include content-block-absolute;
    color: $color-purple;
    height: 1px;
    max-width: 100%;
}

@mixin CMS-lines {
    @include content-block-absolute;
    background: url('../img/icons/CMS-lines.png') no-repeat center / cover;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 295px;
    height: 73px;
}

@mixin footer-phone-icon {
    @include content-block-absolute;
    background: url('../img/icons/call.svg') no-repeat center / cover;
    width: 16px;
    height: 16px;
    left: 0;
    top: 5px;
}

@mixin footer-mail-icon {
    @include content-block-absolute;
    background: url('../img/icons/mail.svg') no-repeat center / cover;
    width: 16px;
    height: 16px;
    left: 0;
    top: 10px;
}

@mixin line-circle {
    @include content-block-absolute;
    background: url('../img/icons/line-circle.svg') no-repeat center / cover;
    width: 14px;
    height: 37px;
    @include center;
}

@mixin open-code-icon {
    @include content-block-absolute;
    background: url('../img/icons/what-choose_open-code.svg') no-repeat center / cover;
    width: 14px;
    height: 22px;
    right: 100px;
    bottom: 25px;
}

@mixin open-code-icon {
    @include content-block-absolute;
    background: url('../img/icons/what-choose_open-code.svg') no-repeat center / cover;
    width: 35px;
    height: 35px;
    right: 100px;
    bottom: 25px;
}

@mixin mobios-icon {
    @include content-block-absolute;
    background: url('../img/icons/question-answer_icon-mobios.png') no-repeat center / cover;
    width: 35px;
    height: 35px;
    left: 0;
    top: 0;
} @mixin question-icon {
    @include content-block-absolute;
    width: 53px;
    height: 53px;
    left: 0;
    top: 0;
} @mixin question-icon-1 {
    @include question-icon;
    background: url('../img/icons/question-answer_icon-1.png') no-repeat center / cover;
}


//animation
@mixin animation-spin {
    animation: spin 1.1s ease 0s infinite alternate;
    @keyframes spin {
        0% {
            opacity: 0.5;
            bottom: 136px;
        }
        100% {
            opacity: 1.0;
            bottom: 180px;
        }
    }
}

@mixin animation-pulse {
    animation: pulse 1s ease 0s infinite alternate;    
    @keyframes pulse {
        0% {
            opacity: 0.5;
        }
        100% {
            opacity: 1.0;
        }
    }
}
//end animation