/* Слайдер с точками */ 
.slick-slider.slick-dotted {

}

/* Ограничивающая оболочка */ 
.slick-list {
    overflow: hidden;
} 
/* Лента слайдов */ 
.slick-track{
    @include classic-flex;
    align-items: flex-start;
    justify-content: flex-start;
}

/* Точки (булиты) */ 
.banner__slider .slick-dots {
    @include classic-flex;
    justify-content: center;
    position: relative;
} 

/* Активная точка */
.slick-dots li.slick-active {
    @include animation-pulse;
} 
.slick-dots li.slick-active button{

}

/* Элемент точки */ 
.banner__slider .slick-dots li button{
    background-color: $color-success;
    font-size: 0;
    width: 15px;
    height: 15px;
    margin: 0 10px;
    border-radius: 50%;
}


/* 

====== Zoom effect ======

*/

.mfp-zoom-in {
  
  /* start state */
  .mfp-with-anim {
    opacity: 0;
    transition: all 0.2s ease-in-out; 
    transform: scale(0.8); 
  }
  
  &.mfp-bg {
    opacity: 0;
	  transition: all 0.3s ease-out;
  }
  
  /* animate in */
  &.mfp-ready {
    .mfp-with-anim {
      opacity: 1;
      transform: scale(1); 
    }
    &.mfp-bg {
      opacity: 0.8;
    }
  }
  
  /* animate out */
  &.mfp-removing {
    
    .mfp-with-anim {
      transform: scale(0.8); 
      opacity: 0;
    }
    &.mfp-bg {
      opacity: 0;
    }
    
  } 
}
